





































import { Component, Vue, Prop, Watch } from "vue-property-decorator";

const color: any = {
    // 背景色、边框色、前景色
    success: ["#f0f9eb", "#e1f3d8", "#67C23A"],
    info: ["#edf2fc", "#EBEEF5", "#909399"],
    warning: ["#fdf6ec", "#faecd8", "#E6A23C"],
    error: ["#fef0f0", "#fde2e2", "#F56C6C"],
};

@Component({
    name: "VMessage",
})
export default class VMessage extends Vue {
    visible = false;
    message = ""; // 消息文字
    duration = 2000; // 显示时间, 毫秒。设为 0 则不会自动关闭
    type = "info"; // 主题
    customClass = ""; // 自定义类名
    onClose: Function | undefined = undefined; // 关闭时的回调函数, 参数为被关闭的 message 实例
    showClose = true; // 是否显示关闭按钮
    closed = false;
    verticalOffset = 20; // Message 距离窗口顶部的偏移量
    timer: number | undefined = undefined;
    dangerouslyUseHTMLString = false; // 是否将 message 属性作为 HTML 片段处理
    center = false; // 文字是否居中

    // 弹窗位置
    get positionStyle() {
        return {
            top: this.verticalOffset + "px",
        };
    }
    // 颜色配置
    get colorStyle() {
        return {
            "background-color": color[this.type][0],
            "border-color": color[this.type][1],
            color: color[this.type][2],
        };
    }
    // 图标类型
    get iconType() {
        switch (this.type) {
            case "success":
                return "mdi-check-circle";
            case "info":
                return "mdi-information";
            case "warning":
                return "mdi-alert-circle";
            case "error":
                return "mdi-close-circle";
        }
        return "mdi-information";
    }

    @Watch("closed")
    closedWatch(newVal: boolean) {
        if (newVal) {
            this.visible = false;
        }
    }

    // 移除对象
    handleAfterLeave() {
        this.$destroy(); // true
        (this.$el as any).parentNode.removeChild(this.$el);
    }
    // 关闭
    close() {
        this.closed = true;
        if (typeof this.onClose === "function") {
            this.onClose(this);
        }
    }
    // 清除计时
    clearTimer() {
        clearTimeout(this.timer);
    }
    // 开始计时
    startTimer() {
        if (this.duration > 0) {
            this.timer = setTimeout(() => {
                if (!this.closed) {
                    this.close();
                }
            }, this.duration);
        }
    }
    // 键盘事件
    keydown(e: KeyboardEvent) {
        if (e.keyCode === 27) {
            // esc关闭消息
            if (!this.closed) {
                this.close();
            }
        }
    }

    mounted() {
        this.startTimer();
        document.addEventListener("keydown", this.keydown);
    }
    beforeDestroy() {
        document.removeEventListener("keydown", this.keydown);
    }
}
